[data-type="bsx-blocks/button"] {
  display: inline-block;
  vertical-align: top; }

[data-type="bsx-blocks/button"] ~ .block-list-appender {
  display: inline-block;
  width: auto;
  margin-left: 1em;
  margin-top: 2em;
  vertical-align: middle; }
  [data-type="bsx-blocks/button"] ~ .block-list-appender > .block-list-appender__toggle {
    padding: 2px;
    border-radius: 50%;
    outline: none;
    background: transparent; }
